<template>
  <div class="preview">
    <iframe
      style="width:100%;height:100%;"
      :src="webRoot + 'pdf_viewer/web/viewer.html?file=' + pdfUrl"
      frameborder="0"
    ></iframe>
    <button
      v-if="params.id"
      class="back"
      :disabled="btnDisabled"
      :class="{'btnDisabled':btnDisabled}"
      @click="end"
    >
      我已阅读
      <span class="countdown" ref="countdown">(10s)</span>
    </button>
    <!-- <button class="back" @click="back">返 回</button> -->
  </div>
</template>

<script>
export default {
  name: "Preview",
  data() {
    return {
      pdfUrl: "",
      params: {
        id: null,
        busiCode: null,
        token: null
      },
      maxtime: 3,
      webRoot: process.env.VUE_APP_BASE_PATH,
      btnDisabled: true
    };
  },
  props: {
    url: {
      type: String,
      require: false
    }
  },
  methods: {
    getFormatUrl(url) {
      var params = url.split("?")[1];
      if (params) {
        for (var k of params.split("&")) {
          var key = k.split("=")[0];
          var value = k.split("=")[1];
          if (key === "pdfUrl") {
            return value;
          }
        }
      }
    },
    countDown() {
      if (this.maxtime > 0) {
        --this.maxtime;
        var msg = "(" + this.maxtime + "s)";
        this.$refs.countdown.innerText = msg;
      } else {
        clearInterval(this.timer);
        this.btnDisabled = false;
        this.$refs.countdown.style.display = "none";
      }
    },
    end() {
      var iframe = document.getElementsByTagName("iframe")[0];
      var ifrDoc = iframe.contentWindow.document;
      if (!ifrDoc || !ifrDoc.getElementById("pageNumber")) {
        return;
      }
      var currentPage = ifrDoc.getElementById("pageNumber").value;
      var totalPage = ifrDoc.getElementById("numPages").innerText.substring(2);
      // iframe.contentWindow.location.reload(true);
      console.log('currentpage'+currentPage);
      console.log('totalpage'+totalPage);
      
      if (totalPage !== "0") {
        if (currentPage === totalPage) {
          this.$router.push("/");
        } else {
          this.$toast("请先阅读完以上内容", "error", 2000);
        }
      }

      //this.$axios.post('',{});
    },
    back() {
      window.history.go(-1);
    }
  },
  mounted() {
    this.pdfUrl = this.getFormatUrl(window.location.href);
    console.log(this.$route.query);
    if (this.$route.query.id) {
      this.params.id = this.$route.query.id;
      this.$nextTick(() => {
        this.timer = setInterval(this.countDown, 1000);
      });
    }
    if (this.$route.query.busiCode) {
      this.params.busiCode = this.$route.query.busiCode;
    }
    if (this.$route.query.id) {
      this.params.token = this.$route.query.token;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
  text-decoration: none;
}
.preview {
  width: 100%;
  height: 100%;
}
.back {
  background: #df614c;
  color: #fff;
  border-radius: 10px;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100px;
  line-height: 100px;
  border: none;
  outline: none;
}
.btnDisabled {
  background: #cccccc;
}
</style>
